import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";

import PurchaseOrderUpdateForm from "./updateForm";
import HistoryPurchaseOrder from "./history";

import notification from "../../../../components/notification";
import { Spin, Tooltip } from "antd";

import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/purchase/purchaseOrder/actions";
import { Box } from "../../../App/app.style";
import Print from "./print";

const redirecTo = "/dashboard/purchases/purchase-orders";

class UpdatePurchaseOrder extends Component {
  state = {
    visible: false
  };

  componentDidMount() {
    const { editPurchaseOrder } = this.props;
    const { id } = this.props.match.params;
    editPurchaseOrder(id);
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.purchaseOrder.error) {
      notification("error", this.props.purchaseOrder.error);
      this.props.clearError();
    }
    if (this.props.purchaseOrder.success) {
      // this.props.history.replace(
      //   redirecTo + "/update/" + this.props.purchaseOrder.id_purchase_order
      // );
      //   this.props.history.replace(redirecTo);
      notification("success", this.props.purchaseOrder.success);
      this.props.clearSuccess();
    }
    // if (this.props.purchaseOrder.warning) {
    //   // this.props.history.replace(
    //   //   redirecTo + "/update/" + this.props.purchaseOrder.id_purchase_order
    //   // );
    //   //   this.props.history.replace(redirecTo);
    //   notification("warning", this.props.purchaseOrder.warning);
    //   this.props.clearSuccess();
    // }
  }

  handleModalHistory = () => {
    const { visible } = this.state;
    visible
      ? this.setState({ visible: false })
      : this.setState({ visible: true });
  };

  handleSubmit = value => {
    const { updatePurchaseOrder } = this.props;
    const { id } = this.props.match.params;
    updatePurchaseOrder(id, value);
  };

  handleSubmitClose = value => {
    const { closePurchaseOrder } = this.props;
    const { id } = this.props.match.params;

    closePurchaseOrder(id, value);
  };

  handleSubmitCancel = value => {
    const { cancelPurchaseOrder } = this.props;
    const { id } = this.props.match.params;

    cancelPurchaseOrder(id, value);
  };

  handleSubmitUnconfirm = value => {
    const { unconfirmPurchaseOrder } = this.props;
    const { id } = this.props.match.params;

    unconfirmPurchaseOrder(id, value);
  };

  render() {
    // console.log(this.props);
    const { loading, loadingSubmit } = this.props.purchaseOrder;
    const purchaseOrder = this.props.purchaseOrder.purchaseOrder
      ? this.props.purchaseOrder.purchaseOrder
      : [];
    return (
      <LayoutWrapper>
        <Box>
          <PageHeader
            title={<IntlMessages id="purchaseOrders.update.title" />}
            marginBottom={true}
          >
            <Tooltip placement="top" title="Info">
              <Button
                style={{
                  fontSize: "24px",
                  border: 0
                  // color: theme.palette.warning
                }}
                icon="issues-close"
                onClick={() => this.handleModalHistory()}
                loading={loading}
              >
                {/* <IntlMessages id="itemRequests.history" /> */}
              </Button>
            </Tooltip>

            <Print loading={loading} data={purchaseOrder} />

            <Button
              style={{ marginLeft: "5px" }}
              onClick={() => this.props.history.replace(redirecTo)}
            >
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>

          <HistoryPurchaseOrder
            visible={this.state.visible}
            handleModalHistory={this.handleModalHistory}
            description={purchaseOrder ? purchaseOrder.description : null}
          />

          {loading && !loadingSubmit ? (
            <div className="spinStyle">
              <Spin size="large" />
            </div>
          ) : (
            <PurchaseOrderUpdateForm
              submit={this.handleSubmit}
              purchaseOrder={this.props.purchaseOrder}
              params={this.props.match.params}
              handleSubmitClose={this.handleSubmitClose}
              handleSubmitCancel={this.handleSubmitCancel}
              handleSubmitUnconfirm={this.handleSubmitUnconfirm}
            />
          )}
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    purchaseOrder: state.purchaseOrder,
    auth: state.auth
  };
};

export default compose(
  connect(
    mapStateToProps,
    actions
  )
)(UpdatePurchaseOrder);
